import Vue from 'vue';
import com from './index.vue';

const Animate = Vue.extend(com);

export default function ({
  startX,
  startY,
  endX,
  endY,
  src,
  width,
  height,
}) {
  const app = new Animate({
    el: document.createElement('div'),
    data() {
      return {
        moveX: startX,
        moveY: startY,
        sx: 1,
        sy: 1,
        opacity: 1,
        exist: true, // ç¨æ¥å¤æ­æ¯å¦å±ç¤ºçï¼æ¹ä¾¿åé¢æ¥éæ¯ç»ä»¶
        src,
        width,
        height,
      };
    },
  });

  // æå¥å°å¨å± DOM ä¸­
  document.body.appendChild(app.$el);

  // ç¨äºååæ ·å¼
  setTimeout(() => {
    app.moveX = endX;
    app.moveY = endY;
    app.sx = 0.1;
    app.sy = 0.1;
    app.opacity = 0;
  }, 0);

  // éæ¯ç»ä»¶
  setTimeout(() => {
    app.exist = false;
  }, 1100);
}
