<template>
  <div id="card">
    <div class="card-img">
      <img :src="data.images[0]" ref="img" />
    </div>
    <div class="card-content">
      <div class="title van-ellipsis">{{ data.title }}</div>
      <div class="desc van-ellipsis">{{ data.desc }}</div>
      <div class="tags van-ellipsis">
        <div class="tags-item" v-for="(item, index) in data.tags" :key="index">
          {{ item }}
        </div>
      </div>
      <div class="price">ï¿¥{{ data.price }}</div>
      <div class="count">
        <div @click="countNum(-1)" v-if="counterMap[data.id]">
          <img
            src="https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/rec.png"
            alt=""
          />
        </div>
        <div class="num" v-if="counterMap[data.id]">
          {{ counterMap[data.id] }}
        </div>
        <div @click="countNum(1)">
          <img
            src="https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/add.png"
            alt=""
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import Animate from '@/tools/animate/index';

export default {
  props: ['data', 'noFly', 'needNum'],
  data() {
    return {};
  },
  computed: {
    ...mapState(['counterMap']),
  },
  created() {
    // console.log(this.counterMap);
  },
  methods: {
    countNum(value) {
      // console.log(value);
      const { id } = this.data;
      const num = this.counterMap[this.data.id];

      if (num === 1 && value === -1 && this.needNum) {
        this.$emit('noMore', num);
        return;
      }
      this.$store.commit('saveLocalStorage', { id, value });

      if (value === -1 || this.noFly) {
        return;
      }

      const { left: imgLeft, top: imgTop } = this.$refs.img.getBoundingClientRect();
      const { offsetWidth: imgWidth, offsetHeight: imgHeight } = this.$refs.img;

      const shopCar = document.getElementById('shop-car');
      const { left: carX, top: carY } = shopCar.getBoundingClientRect();
      const { offsetWidth: carWidth, offsetHeight: carHeight } = shopCar;

      const endX = carX + carWidth / 2;
      const endY = carY + carHeight / 2;

      Animate({
        startX: imgLeft,
        startY: imgTop,
        endX,
        endY,
        src: this.$refs.img.src,
        width: imgWidth,
        height: imgHeight,
      });
    },
  },
};
</script>

