<template>
  <div id="shopping">
    <div class="shopping-header">
      <van-nav-bar
        title="è´­ç©è½¦"
        right-text="å é¤"
        @click-right="del"
      ></van-nav-bar>
    </div>

    <div class="list" v-if="!noData">
      <van-checkbox-group v-model="result" ref="checkboxGroup">
        <div class="list-item" v-for="item in goodsList" :key="item.id">
          <van-checkbox :name="item.id" class="check"></van-checkbox>
          <Card :data="item" :noFly="true" @noMore="noMore" :needNum="true" />
        </div>
      </van-checkbox-group>
    </div>

    <div class="noData" v-else>
      <img
        src="https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/shopping_bg.jpg"
        alt=""
      />
    </div>

    <van-submit-bar
      :price="price"
      :button-text="`å»ç»ç®(${totalNum})`"
      @submit="onSubmit"
    >
      <van-checkbox v-model="checked" @click="checkAll">å¨é</van-checkbox>
    </van-submit-bar>
  </div>
</template>

<script>
import Card from '@/components/Card.vue';
import * as API from '@/api/index';
import { mapState } from 'vuex';
import { Toast, Dialog } from 'vant';

export default {
  components: {
    Card,
  },
  computed: {
    ...mapState(['counterMap']),
    noData() {
      return !(this.goodsList.length > 0);
    },
    totalNum() {
      const result = this.goodsList.filter((item) => this.result.includes(item.id));

      const sum = result.reduce(
        (prev, next) => prev + (this.counterMap[next.id] || 0),
        0,
      );

      return sum;
    },
    price() {
      const result = this.goodsList.filter((item) => this.result.includes(item.id));
      // console.log(result);

      let sum = result.reduce((prev, next) => {
        if (next.price_off) {
          return prev + next.price_off * (this.counterMap[next.id] || 0);
        }
        return prev + next.price * (this.counterMap[next.id] || 0);
      }, 0);
      sum = Math.round(sum * 100);
      // console.log(sum);
      return sum;
    },
  },
  watch: {
    result() {
      if (this.result.length === this.goodsList.length) {
        this.checked = true;
      } else {
        this.checked = false;
      }
    },
  },
  data() {
    return {
      result: [],
      goodsList: [],
      checked: false,
    };
  },
  async created() {
    const ids = Object.keys(this.counterMap);
    if (ids.length === 0) {
      return;
    }
    const res = await API.getGoodsByIds(ids.join());
    // console.log(res);
    this.goodsList = [...res.data.list];
  },
  methods: {
    checkAll() {
      if (this.noData) {
        Toast('è´­ç©è½¦æ²¡æä»»ä½ææåå');
        this.checked = false;
        return;
      }
      if (this.checked) {
        this.$refs.checkboxGroup.toggleAll(true);
      } else {
        this.$refs.checkboxGroup.toggleAll(false);
      }
    },
    onSubmit() {
      // return "onSubmit";
    },
    async del() {
      if (this.result.length === 0) {
        Toast('æ¨è¿æ²¡éæ©åå');
        return;
      }
      try {
        await Dialog.confirm({
          message: 'æ¨æ¯å¦è¦å é¤å·²éä¸­åå',
        });

        this.result.forEach((id) => {
          this.$store.commit('saveLocalStorage', { id, value: -Infinity });
          this.goodsList = this.goodsList.filter(
            (item) => !this.result.includes(item.id),
          );
        });
        this.result = [];
      } catch (error) {
        Toast('æ¨ç¹äºåæ¶å é¤åå');
      }
    },
    noMore() {
      Toast('è¯¥ååä¸è½åå°äº');
    },
  },
};
</script>

